<template>
  <BaseTemplate>
    <!--  -->
    <section class="cart-section">
      <h2></h2>
      <div v-if="cartItems.length === 0"></div>
      <div v-else class="cart-items-container">
        <div
          class="cart-item-card"
          v-for="item in cartItems"
          :key="item.product.product_id"
        >
          <div class="cart-item-content">
            <!--  src  -->
            <img
              :src="`${mediaUrl}${item.product.image}`"
              alt=""
              class="cart-item-image"
              @click="goToProductDetail(item.product.product_id)"
            />
            <div class="cart-item-details">
              <h4 class="cart-item-name">{{ item.product.product_name }}</h4>
              <p>: {{ item.product.price }} </p>
              <p>
                :
                <button @click="decreaseQuantity(item)">-</button>
                <span>{{ item.quantity }}</span>
                <button @click="increaseQuantity(item)">+</button>
              </p>
              <p>
                : {{ (item.product.price * item.quantity).toFixed(2) }} 
              </p>
              <button class="btn btn-danger" @click="removeFromCart(item)">
                
              </button>
            </div>
          </div>
        </div>
        <div class="cart-summary">
          <p>: {{ totalAmount.toFixed(2) }} </p>
          <button class="btn btn-primary" @click="checkout"></button>
        </div>
      </div>
    </section>
  </BaseTemplate>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      mediaUrl: "http://localhost:8000", //  URL /media/
    };
  },
  computed: {
    //  Vuex 
    ...mapGetters(["cartItems", "totalAmount"]),
  },
  mounted() {
    this.fetchCartItems(); // 
  },
  methods: {
    //  Vuex actions 
    ...mapActions([
      "fetchCartItems",
      "addItemToCart",
      "removeItemFromCart",
      "clearCartItems",
    ]),

    increaseQuantity(item) {
      this.addItemToCart({ ...item, quantity: 1 }); // 
    },

    decreaseQuantity(item) {
      if (item.quantity > 1) {
        this.addItemToCart({ ...item, quantity: -1 }); // 
      }
    },

    removeFromCart(item) {
      this.removeItemFromCart(item.product.product_id); // 
    },

    checkout() {
      this.$router.push({ name: "OrderConfirmation" }); // 
    },

    goToProductDetail(productId) {
      this.$router.push({ name: "ProductDetail", params: { id: productId } }); // 
    },
  },
};
</script>

<style scoped>
/*  */
.cart-items-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.cart-section {
  @extend .glass-effect;
  padding: 2rem;
  max-width: 1200px;
  margin: 2rem auto;
}

.cart-item-card {
  display: flex;
  align-items: center;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  min-width: 600px;
}

.cart-item-content {
  display: flex;
  align-items: center;
  width: 100%;
}

.cart-item {
  display: flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.7);
  padding: 10px;
  border-radius: 10px;
}

.cart-item-image {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
  margin-right: 1rem;
  cursor: pointer; /*  */
}

.cart-item-details {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.cart-item-name {
  font-size: 1.2rem;
  font-weight: bold;
}

.cart-item-actions {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}

.cart-item-actions button {
  padding: 5px 10px;
  border: 1px solid #ccc;
  background-color: #fff;
  cursor: pointer;
  transition: background 0.3s;
}

.cart-item-actions button:hover {
  background-color: #f0f0f0;
}

.cart-item-price {
  margin-top: 10px;
  font-weight: bold;
}

.cart-summary {
  margin-top: 2rem;
  text-align: right;
  font-size: 1.5rem;
  font-weight: bold;
}
</style>
