<template>
  <div class="app-container">
    <!--  -->
    <nav class="navbar glass-navbar">
      <a class="navbar-brand" href="#">
        <img src="../assets/logo.png" alt="" class="navbar-logo" />
        <span class="brand-name"></span>
      </a>
    </nav>

    <!--  -->
    <section class="login-section">
      <div class="login-card">
        <!-- Logo -->
        <img src="../assets/logo.png" alt="" class="login-logo" />

        <!--  -->
        <h2 class="login-title"></h2>

        <form @submit.prevent="register">
          <div class="form-group">
            <label for="username"></label>
            <input
              type="text"
              id="username"
              v-model="username"
              required
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label for="password"></label>
            <input
              type="password"
              id="password"
              v-model="password"
              required
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label for="confirmPassword"></label>
            <input
              type="password"
              id="confirmPassword"
              v-model="confirmPassword"
              required
              placeholder=""
            />
            <p v-if="passwordMismatch" class="error-message">
              
            </p>
          </div>
          <div class="form-group">
            <label for="phone"></label>
            <input
              type="text"
              id="phone"
              v-model="phone"
              required
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label for="email"></label>
            <input
              type="email"
              id="email"
              v-model="email"
              required
              placeholder=""
            />
          </div>

          <div class="form-group terms-checkbox">
            <label for="terms">
              <input type="checkbox" id="terms" v-model="agreedToTerms" />
              
              <a href="#" @click.prevent="showTermsModal"></a>
            </label>
          </div>
          <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>

          <!--  -->
          <button
            type="submit"
            class="btn btn-primary login-button"
            :disabled="!agreedToTerms"
          >
            
          </button>
        </form>

        <!--  -->
        <div class="additional-actions">
          <router-link to="/login" class="small-text"
            >? </router-link
          >
        </div>
      </div>
    </section>

    <!--   -->
    <div v-if="showModal" class="modal-overlay" @click="closeModal">
      <div class="modal-content" @click.stop>
        <h3></h3>
        <p><strong></strong></p>
        <p></p>
        <p>
          
        </p>
        <p><strong></strong></p>
        <p></p>
        <p></p>
        <p>
          
        </p>
        <button class="btn btn-success" @click="closeModal"></button>
      </div>
    </div>

    <!--  -->
    <footer class="footer">
      <div class="container">
        <span>&copy; 2024 anka. .</span>
      </div>
    </footer>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      username: "",
      password: "",
      confirmPassword: "",
      phone: "",
      email: "",
      agreedToTerms: false,
      showModal: false,
      passwordMismatch: false,
      errorMessage: "", // 
    };
  },
  watch: {
    confirmPassword() {
      this.passwordMismatch = this.password !== this.confirmPassword;
    },
  },
  methods: {
    async register() {
      if (this.passwordMismatch) {
        this.errorMessage = "";
        return;
      }

      try {
        const response = await axios.post(
          "http://127.0.0.1:8000/api/register/",
          {
            username: this.username,
            password: this.password,
            confirm_password: this.confirmPassword,
            phone_number: this.phone,
            email: this.email,
          }
        );

        if (response.status === 201) {
          this.$router.push("/login");
        }
      } catch (error) {
        if (error.response && error.response.data) {
          const errors = error.response.data;
          if (errors.username) {
            this.errorMessage = "";
          } else if (errors.email) {
            this.errorMessage = "";
          } else {
            this.errorMessage = "";
          }
        } else {
          this.errorMessage = "";
        }
      }
    },

    showTermsModal() {
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
    },
  },
};
</script>

<style scoped>
.app-container {
  min-height: 100vh;
  background: url("../assets/background.jpg") no-repeat center center fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
}

.glass-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  position: sticky;
  top: 0;
  z-index: 1000;
  height: 60px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.brand-name {
  font-size: 1.5rem;
  color: #007bff;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.login-section {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-card {
  background: rgba(255, 255, 255, 0.9);
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
  text-align: center;
}

.login-logo {
  max-width: 100px;
  margin-bottom: 1rem;
}

.login-title {
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.form-group input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.login-button {
  width: 100%;
  padding: 0.75rem;
  font-size: 1.25rem;
}

.additional-actions {
  margin-top: 1rem;
}

.small-text {
  font-size: 0.875rem;
  color: #007bff;
  text-decoration: none;
}

.small-text:hover {
  text-decoration: underline;
}

.error-message {
  color: red;
  margin-top: 0.5rem;
  font-size: 0.875rem;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  max-width: 600px;
  width: 100%;
  text-align: left;
  overflow-y: auto;
  max-height: 80vh;
  backdrop-filter: blur(10px);
}

.modal-content button {
  display: block;
  margin: 1rem auto 0;
  padding: 0.5rem 1rem;
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
</style>
